.login {
  position: relative;
  width: 100%;
  height: 100vh;
  background-color: #295caa;
  background-position: center;
  background-size: contain;
  overflow: hidden;
}
.login .login-logo {
  height: 200px;
}
.login .login-main {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 400px;
  margin: 0 auto;
  padding: 20px 60px;
  background-color: rgba(255, 255, 255, 0.5);
  box-shadow: 0 0 2px 5px rgba(255, 255, 255, 0.3);
  border-radius: 6px;
}
.login .login-main .logo {
  padding: 20px 0;
  margin-bottom: 20px;
  text-align: center;
  font-weight: 700;
  color: #295caa;
}
.login .login-main .logo .logo-text {
  font-size: 28px;
}
.login .login-main .logo .logo-en {
  font-size: 14px;
}
.login .login-main .login-form-forgot {
  float: right;
}
.login .ant-btn + .ant-btn {
  margin-top: 10px;
}
.login .login-footer {
  position: absolute;
  left: 50%;
  bottom: 50px;
  transform: translateX(-50%);
  font-size: 12px;
  text-align: center;
  color: rgba(255, 255, 255, 0.8);
}
.ant-btn-primary {
  background-color: #295caa;
  border-color: #295caa;
}
